body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
    height: 100%;
    background-color: #ECF0F1;
}

div {
    font-family: 'Poppins', sans-serif;
}

.App {
    padding-top: 65px;
    text-align: center;
}

a {
    color: unset;
    text-decoration: none;
}

img {
    max-width: 100%;
    max-height: 100%;
}

.card-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}

.card-detail {
    height: 420px;
    display: -webkit-flex;
    display: flex;

    padding: 10px;
    margin: 10px;

    -webkit-align-items: center;

            align-items: center;
    position: relative;
}

.card-detail .description {
    height: 100%;
}

.card-detail .paragraph {
    padding: 5px 20px;
}

.card-detail img {
    max-width: 45%;
    max-height: 100%;
    object-fit: contain;
}

.card-wrapper {
    margin: 20px;
}

.card {
    width: 350px;
}

.card img {
    margin: auto;
    max-height: 200px;
    width: auto;
}

.card-content {
    height: 350px;
    overflow: hidden;
}

.price-tag {
    pointer-events: none;
}

.paragraph {
    text-align: left;
}

.spinner {
    width: 60px;
    height: 60px;
}

.empty-cart {
    margin: 20px;
    padding: 10px;
    height: 200px;
}

.icon-extra-large.icon-extra-large {
    font-size: 60px;
}

.icon-cover.icon-cover {
    font-size: 100px;
}

.cart {
    margin: 20px;
    min-height: 460px;
    transition: height 200ms;
}

.cart-content {
    min-height: 300px;
}

.cart-item {
    width: 100%;
    height: 125px;
    display: -webkit-flex;
    display: flex;
}

.cart-item-image.cart-item-image {
    width: 30%;
}

.cart-item-content.cart-item-content {
    text-align: left;
    width: 40%;
    padding: 16px;
}

.cart-item-action {
    width: 30%;
}

.checkout-form-wrapper {
    text-align: left;
    width: 300px;

    max-height: 100%;
    margin: auto;
}

.checkout-form {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: baseline;
            align-items: baseline;
    padding: 10px;
}

.input-security-code {
    width: 50px;
    white-space: nowrap;
}

.checkout-complete {
    color: green;
    display: block;

    padding: 10px;
}

.cart-action-wrapper {
    -webkit-flex-direction: row-reverse;
            flex-direction: row-reverse;
}

.cart-action {
    padding: 10px 50px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.cart-action button {
    margin: 10px;
}

.filter-form {
    margin: 20px;
    padding: 0;

    display: -webkit-flex;

    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}

.filter-select {
    margin: 10px;
}

.app-toolbar {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

